import "./styles/main.scss";
import wishlist from "./assets/wishlist.svg";
import photo from "./assets/object-photo.png";
import turkey from "./assets/turkey.svg";
import location from "./assets/town.svg";
import characteristics from "./assets/characteristics.svg";

const wishlistImg = document.getElementById("wishlist");
wishlistImg.src = wishlist;

const thumbImg = document.getElementById("thumb");
thumbImg.src = photo;

const turkeyImg = document.getElementById("turkey");
turkeyImg.src = turkey;

const locationImg = document.getElementById("location");
locationImg.src = location;

const characteristicsImg = document.getElementsByClassName("characteristics_img");
for (let i = 0; i < characteristicsImg.length; i++) {
    characteristicsImg[i].src = characteristics;
}

const tabs = document.querySelectorAll('[data-tab-target]')
const tabContents = document.querySelectorAll('[data-tab-content]')

tabs.forEach(tab => {
    tab.addEventListener('click', () => {
        const target = document.querySelector(tab.dataset.tabTarget)
        tabContents.forEach(tabContent => {
            tabContent.classList.remove('active')
        })
        tabs.forEach(tab => {
            tab.classList.remove('active')
        })
        tab.classList.add('active')
        target.classList.add('active')
    })
})